.header {
  position: fixed;
  width: 100%;
  top: 0px;
  backdrop-filter: blur(5px);
  z-index: 10;
}

.header__wrapper {
  display: flex;
  align-items: center;
}

@media (max-width: 767px) {
  .header__wrapper {
    flex-wrap: wrap;
  }
}

@media (max-width: 1279px) {
  .header__wrapper img {
    width: 180px;
  }
}

.header__contact {
  font-size: 14px;
  line-height: 16px;
  font-weight: 300;
  margin-left: auto;
}

@media (max-width: 767px) {
  .header__contact {
    order: -1;
    width: 100%;
    font-size: 12px;
    line-height: 14px;
    margin-top: 10px;
    margin-bottom: 5px;
    display: flex;
    justify-content: space-between;
  }
}

.header__contact-number {
  margin-right: 40px;
}

.header__contact-mail {
  margin-right: 40px;
}

@media (max-width: 767px) {
  .header__contact-mail {
    margin-right: 0px;
  }
}

.header__language {
  display: flex;
}

@media (max-width: 767px) {
  .header__language {
    margin-left: auto;
  }
}

.header__language-item {
  width: 36px;
  height: 36px;
  border: 1px solid transparent;
  border-radius: 50%;
  position: relative;
  cursor: pointer;
}

.header__language-item a {
  color: #646482;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 12px;
}

.header__language-item.active {
  border-color: #fff;
}

.header__language-item.active a {
  color: #fff;
}

html * {
  box-sizing: border-box;
}

body {
  background-color: #010119;
  color: #fff;
  font-family: 'Ubuntu', sans-serif;
  position: relative;
}

.container {
  max-width: 1248px;
  width: 100%;
  padding: 0 30px;
  margin: 0 auto;
}

@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

@media (max-width: 1279px) {
  .container.technology {
    padding: 0 100px;
  }
}

@media (max-width: 767px) {
  .container.technology {
    padding: 0 16px;
  }
}

h1 {
  margin: 0;
  padding: 0;
}

h2 {
  margin: 0;
  padding: 0;
}

h3 {
  margin: 0;
  padding: 0;
}

p {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: #ffffff;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style-type: none;
}

.main {
  background-color: #4045ba;
}

.top__image {
  position: absolute;
  top: -370px;
  right: -409px;
  z-index: -10;
}

@media (max-width: 1279px) {
  .top__image {
    top: -153px;
    right: -370px;
  }
}

@media (max-width: 767px) {
  .top__image {
    top: -85px;
    right: -130px;
  }
}

@media (max-width: 1279px) {
  .top__image img {
    width: 645px;
    height: 645px;
  }
}

@media (max-width: 767px) {
  .top__image img {
    width: 375px;
    height: 375px;
  }
}

.top {
  position: relative;
  padding-top: 240px;
  overflow-x: hidden;
  padding-bottom: 290px;
}

@media (max-width: 1279px) {
  .top {
    padding-top: 120px;
    padding-bottom: 160px;
  }
}

@media (max-width: 1279px) {
  .top__item {
    margin-left: 90px;
  }
}

@media (max-width: 767px) {
  .top__item {
    margin-left: 0px;
  }
}

.top__item-title {
  font-size: 72px;
  line-height: 82px;
  font-weight: 700;
  max-width: 660px;
  width: 100%;
  height: 170px;
}

@media (max-width: 1279px) {
  .top__item-title {
    height: 95px;
  }
}

@media (max-width: 767px) {
  .top__item-title {
    max-width: 215px;
  }
}

.top__item-title span {
  color: #00a2ff;
}

@media (max-width: 1279px) {
  .top__item-title {
    font-size: 40px;
    line-height: 46px;
    width: 380px;
  }
}

@media (max-width: 767px) {
  .top__item-title {
    font-size: 24px;
    line-height: 28px;
    width: 100%;
  }
}

.top__item-text {
  font-size: 24px;
  line-height: 36px;
  max-width: 515px;
  width: 100%;
  margin-top: 10px;
}

@media (max-width: 1279px) {
  .top__item-text {
    font-size: 18px;
    line-height: 27px;
    width: 400px;
  }
}

@media (max-width: 767px) {
  .top__item-text {
    width: 100%;
    font-size: 14px;
    line-height: 21px;
  }
}

.top__item-btn {
  display: flex;
  align-items: center;
  margin-top: 35px;
  position: relative;
  width: 146px;
  cursor: pointer;
}

@media (max-width: 1279px) {
  .top__item-btn {
    margin-top: 25px;
  }
}

@media (max-width: 767px) {
  .top__item-btn {
    margin-top: 15px;
  }
}

.top__item-btn a {
  font-size: 16px;
  line-height: 27px;
  color: #019BE0;
}

.top__item-btn img {
  margin-left: 10px;
  position: relative;
}

.top__item-btn span {
  width: 26px;
  height: 26px;
  border: 1px solid #fff;
  border-radius: 50%;
  display: inline;
  margin-left: -17px;
  position: absolute;
  top: 0;
  right: 0;
  transition: all .4s;
}

.top__item-btn:hover span {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  border: 1px solid #019BE0;
  top: -13px;
  right: -13px;
}

.top__item-btn.works-btn {
  width: 155px;
}

.top__item-btn.footer-btn {
  margin-left: auto;
  margin-top: 10px;
}

.top__item-btn.send {
  width: 93px;
  margin-left: auto;
  margin-top: 55px;
}

.top__item-btn.send span {
  width: 26px;
  height: 26px;
  border: 1px solid #000;
}

.top__item-btn.send:hover span {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  border: 1px solid #019BE0;
  top: -13px;
  right: -13px;
}

.social-networks {
