.modal-start {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
  position: relative;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 20;
  display: none;
  padding-left: 10px;
  padding-right: 10px;
}

.modal-start.active {
  display: block;
}

.modal-start__item {
  max-width: 788px;
  width: 100%;
  height: 526px;
  background-color: #fff;
  position: relative;
  padding-top: 80px;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 20px;
  margin: 0 auto;
  top: 17%;
}

.modal-start__item:before {
  position: absolute;
  content: url("../img/modal-start-bg.svg");
  right: 0;
  top: 0;
  z-index: 10;
}

.modal-start__item-close {
  position: absolute;
  width: 35px;
  height: 35px;
  border: 2px solid #fff;
  border-radius: 50%;
  background: none;
  outline: none;
  right: -60px;
  top: -50px;
  cursor: pointer;
}

@media (max-width: 990px) {
  .modal-start__item-close {
    right: 0px;
  }
}

@media (max-width: 767px) {
  .modal-start__item-close {
    top: -50px;
  }
}

.modal-start__item-close:before {
  position: absolute;
  content: url("../img/close.png");
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-start__item-inner {
  max-width: 585px;
  width: 100%;
  margin: 0 auto;
  z-index: 10;
}

.modal-start__title {
  font-size: 40px;
  line-height: 46px;
  font-weight: 700;
  color: #000;
}

@media (max-width: 767px) {
  .modal-start__title {
    font-size: 18px;
  }
}

.modal-start__form-name, .modal-start__form-mail, .modal-start__form-about {
  width: 100%;
  margin-top: 60px;
  line-height: 31px;
  padding: 0 10px;
  font-size: 14px;
  font-weight: 300;
  color: #010119;
  border: none;
  border-bottom: 1px solid #010119;
  opacity: 0.5;
  outline: none;
}

.modal-start__form-mail {
  margin-top: 37px;
}

.modal-start__form-about {
  margin-top: 42px;
}
